export default{
	data(){
		return {
			newscarobj:{},
			newcararr:[],
			len:""
		}
	},
	methods:{
		//进入店铺
		Enterthestore(){
			uni.navigateTo({
				url:"/pages/Store/store"
			})
		},
		shoppingcart(){
			uni.switchTab({
				url:"/pages/shoppingCart/shoppingCart"
			})
		},
		Addcart(obj,img){
			//当点击加入到购物车 就加入到缓存中 获取店铺名字 商品的图片 商品的标题 商品的参数(可有可无) 商品的价格
			let {newscarobj,newcararr} = this.$data
			
			newscarobj = obj
			newscarobj.img = img
			this.newscarobj = newscarobj
			newcararr.unshift(newscarobj)
			this.newcararr = newcararr
			if(newcararr.length==0){
				this.len=""
			}else{
				this.len = newcararr.length
			}
			uni.setStorage({
			    key:"Addcart",
			    data:this.newcararr,
			    success:(res)=>{
					console.log(res)
			    },
				fail(err){
					console.log(err)
				}
			});
		},
		Skiporder(){
			//跳转到购买页面
			uni.navigateTo({
				url:"/pages/Purchasepage/Purchasepage"
			})
		}
	},
	props:["pic","imgs"]
}
